aside {
    width: 33%;
    float: right;
    background-color: $blue;
    padding: 50px 60px;
    color: #ffffff;
    border-left: 5px solid rgba(0, 0, 0, 0.25);
    position: relative;
    margin-bottom: 1rem;
    
    &:before {
        display: block;
        content: '';
        height: 50px;
        width: 50px;
        background-color: #ffffff;
        position: absolute;
        top: 70px;
        left: 0px;
        transform: rotate(45deg) translateX(-45px);
    }
    @media only screen and (max-width: 800px) {
        float: none;
        width: 100%;
        margin-bottom: 80px;
        border-left: 0;
        &:before {
            top: -10px;
            left: 50%;
            transform: rotate(45deg)translateX(-50%);
        }
    }
    h2 {
        margin-bottom: 100px;
    }
    .hr {
        height: 1px;
        background-color: #ffffff;
        width: 100%;
        position: absolute;
        top: 100px;
        left: 0;
    }
    .step_block {
        display: block;
        background-color: #fff;
        color: #000000;
        padding: 30px;
        margin-bottom: 80px;
        text-align: left;
        padding-bottom: 60px;
        position: relative;
        border: 1px solid #ffffff;
        @media only screen and (max-width: 800px) and (min-width: 601px) {
            width: 45%;
            display: inline-block;
            margin-right: 5%;
            vertical-align: top;
            min-height: 210px;
            &:last-of-type {
                margin-right: 0;
            }
        }
        @media only screen and (max-width: 600px) {
            
        }
        h3 {
            font-size: 1rem;
            margin-bottom: 1rem;
            line-height: 1.25;
            @media only screen and (max-width: 940px) {
                font-size: .75rem;
                line-height: 1.25;
            }
            @media only screen and (max-width: 800px) {
                font-size: 1rem;
            }
        }
        .block_icon {
            position: absolute;
            display: block;
            top: -20px;
            right: -20px;
        }
        a {
            display: block;
            padding: 20px;
            position: absolute;
            width: 100%;
            left: 0;
            text-align: center;
            color: #ffffff;
            text-decoration: none;
            bottom: 0;
            transition: all ease-out .25s;
            background: rgb(17,121,176); /* Old browsers */
            background: -moz-linear-gradient(top,  rgba(17,121,176,1) 0%, rgba(0,82,147,1) 100%); /* FF3.6-15 */
            background: -webkit-linear-gradient(top,  rgba(17,121,176,1) 0%,rgba(0,82,147,1) 100%); /* Chrome10-25,Safari5.1-6 */
            background: linear-gradient(to bottom,  rgba(17,121,176,1) 0%,rgba(0,82,147,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1179b0', endColorstr='#005293',GradientType=0 ); /* IE6-9 */
            @media only screen and (max-width: 940px) {
                font-size: .65rem;
            }
            @media only screen and (max-width: 800px) {
                font-size: 1rem;
            }
            &:hover {
                background: rgb(17,121,176); /* Old browsers */
                background: -moz-linear-gradient(top,  rgb(12, 98, 144) 0%, rgb(0, 44, 78) 100%); /* FF3.6-15 */
                background: -webkit-linear-gradient(top,  rgb(12, 98, 144) 0%,rgb(0, 44, 78) 100%); /* Chrome10-25,Safari5.1-6 */
                background: linear-gradient(to bottom,  rgb(12, 98, 144) 0%,rgb(0, 44, 78) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1179b0', endColorstr='#005293',GradientType=0 ); /* IE6-9 */
            }
        }
    }
    .form_holder {
        width: 100%;
        margin: 0 auto;
        display: none;
        .mktoForm {
            width: 100% !important;
            .mktoLabel {
                width: 100% !important;
                color: #ffffff;
                font-family: 'Roboto', sans-serif !important;
                font-size: .85rem;
                right: 400 !important;
                margin-top: .1rem
            }
            .mktoGutter {
                display: none;
            }
            .mktoField {
                width: 100% !important;
                background-color: rgba(255,255,255, 0);
                border: 0;
                border-bottom: 2px solid #ffffff;
                margin-bottom: 0;
                padding: 10px 0;
                color: #ffffff;
                outline: none;
                font-size: .85rem;
                font-family: 'Roboto', sans-serif !important;
                transition: all ease-out .25s;
                &:focus {
                    border-bottom-color: darken($blue, 25%);
                    
                }
            }
            .mktoButtonRow {
                width: 100% !important;
                margin-top: 2rem;
            }
            .mktoButtonWrap {
                width: 100% !important;
                display: block !important;
                margin-left: 0 !important;
            }
            .mktoButton {
                width: 100% !important;
                display: block !important;
                background-color: darken($blue, 25%) !important;
                background-image: none !important;
                border: 0 !important;
                font-size: 1rem !important;
                font-family: 'Roboto', sans-serif !important;
                text-transform: uppercase;
                transition: all ease-out .25s !important;
                &:hover {
                    background-color: darken($blue, 30%) !important;
                }
            }
            .mktoRadioList {
            width: 100% !important;
            margin-top: 2rem !important;
            }
            .mktoField[type=radio] {
                display: inline-block !important;
                width: 7% !important;
                float: none !important;
                margin-right: 3% !important;
            }
            .mktoRadioList label {
                display: inline-block !important;
                vertical-align: top !important;
                width: 90% !important;
                margin: 0 !important;
                float: none !important;
                color: #ffffff !important;
            }
            textarea {
                padding: 10px !important;
                border: 2px solid #ffffff !important;
                font-size: .75rem !important;
                margin-top: 1rem !important;
            }
        }
    }
}